
/******************** Start TV Show Carousel Section */
// import tv show data from json
const tvShowList = require("../data/tv-show.json");

const CAROUSEL_GAP = 10;
const tvShowContainer = document.querySelector("#tv-show");
// initialize carousel item
const carouselContainer = tvShowContainer.querySelector(".carousel-inner");
tvShowList.forEach(elem => {
  carouselContainer.innerHTML += `
  <div class="carousel-item">
    <div class="body">
      <button class="button-secondary action"><a href="${elem.url}">Stream</a></button>
      <div class="info">
        <span class="category">${elem.category} - </span>
        <span>${elem.title}</span>
      </div>
    </div>
    <img class="background" src="${elem.imageUrl}" alt="${elem.desc}"
      style="cursor: pointer;">
  </div>
  `;
});
// query carousel items
const carouselItems = tvShowContainer.querySelectorAll(".carousel-item");

// copy image sources from html
const images = Array.from(carouselItems).map(elem => elem.querySelector("img").src);

// listen on prev button click
tvShowContainer
  .querySelector(".carousel-control-prev")
  .addEventListener("click", () => movePrevCarousel());
// listen on prev button click
tvShowContainer
  .querySelector(".carousel-control-next")
  .addEventListener("click", () => moveNextCarousel());

function movePrevCarousel(){
  const imageUrl = images.pop();
  // add the image source at the beginning
  images.unshift(imageUrl);
  // loop all all carousel elements
  carouselItems.forEach((elem) => {
    // add transition to each carousel element
    elem.style.transition = "all 0.3s ease-in-out";
    // get current carousel element width
    const width = elem.offsetWidth;
    // move the carousel element position to the right (width + margin right)
    elem.style.transform = `translateX(${width + CAROUSEL_GAP}px)`;
  });
  
  // define a timeout
  setTimeout(() => {
    // update carousel element
    updateCarousel();
    carouselItems.forEach((elem) => {
      // remove transition from carousel element
      elem.style.transition = "none";
      // move carousel element to the left
      elem.style.transform = "translateX(0)";
    });
  }, 300);

}


function moveNextCarousel(){
  let imageUrl = images.shift();
  // add the image source at the end
  images.push(imageUrl);
  // loop all all carousel elements
  carouselItems.forEach((elem) => {
    // add transition to each carousel element
    elem.style.transition = "all 0.3s ease-in-out";
    // get current carousel element width
    const width = elem.offsetWidth;
    // move the carousel element position to the right (width + margin right)
    elem.style.transform = `translateX(-${width + CAROUSEL_GAP}px)`;
  });
  
  // define a timeout
  setTimeout(() => {
    // update carousel element
    updateCarousel();
    carouselItems.forEach((elem) => {
      // remove transition from carousel element
      elem.style.transition = "none";
      // move carousel element to the left
      elem.style.transform = "translateX(0)";
    });
  }, 300);
}

// upate carousel 
function updateCarousel() {
  // Update image src
  // query all carousel elements
  const items = tvShowContainer.querySelectorAll(".carousel-item");
  // loop each element
  for (let i = 0; i < items.length; i++) {
    const img = items[i].querySelector("img");
    // update img source
    img.src = images[i];
    if(i === 1){
      items[i].classList.add('selected');
    }else{
      items[i].classList.remove('selected');
    }
  }
}

// Refresh carousel
updateCarousel();

// Auto slide image every 5s
setInterval(() => moveNextCarousel(), 5000);

/******************** End TV Show Carousel Section */

/******************* Start TV Series Carousel Section */

// define a gallery playing state
let isPause = false;

// select the gallery container
const famCarouselSlider = document.querySelector(
  "#tv-series .carousel-slider"
);

const tvSeriesController = document.querySelector(
  "#tv-series .carousel-control-button"
);
tvSeriesController.addEventListener('click', () => {
  isPause = !isPause;
  if (!isPause){
    slideTvSeries();
    tvSeriesController.classList.remove("paused");
  }else{
    tvSeriesController.classList.add("paused");
      
  }
});

// update gallery view
const tvSeriesList = require("../data/tv-series.json");
console.log(tvSeriesList);
const wrapper = famCarouselSlider.querySelector(".carousel-inner");
tvSeriesList.forEach(tvSeries => {
  wrapper.innerHTML += `
              <div class="carousel-item">
                <img class="background" src="${tvSeries.imageUrl}" alt="...">
                <div class="body">
                  <div class="info">
                    <span>${tvSeries.title}</span>
                    <span>${tvSeries.category}</span>
                  </div>
                  <div class="action">
                    <button type="button" class="button-secondary"><a href="${tvSeries.url}">Play now</a></button>
                  </div>
                </div>
              </div>
  `;
});
const transitionDuration = 100; // milisecond
const width = wrapper.querySelector(".carousel-item").offsetWidth;
let newWidth = width;
let step = 10;

wrapper.addEventListener('mouseover', () => {
  step = 3;
  console.log('mouse over');
});
wrapper.addEventListener('mouseleave', () => {
  step = 10;
  console.log('mouse out');
});
function slideTvSeries() {
  // select first gallery element
  const firstItem = wrapper.querySelector(".carousel-item");;
  // decrease first gallery element width step by step
  newWidth = newWidth > step ? newWidth - step: 0;
  firstItem.style.width = `${newWidth}px`;

  // define a timeout
  setTimeout(() => {
    // remove and insert the first gallery element back to gallery container (overwrite)
    if(newWidth < step){
      wrapper.appendChild(firstItem);
      firstItem.style.width = `${width}px`;
      newWidth = width;
    }
    if(!isPause){
      slideTvSeries();
    }
    // wait for trasition duration
  }, transitionDuration);
}
// Initialize slide
slideTvSeries();

/******************* End TV Series Carousel Section */
